import React from 'react';
import './SplitInput.css';

const SplitInput = ({ id }) => {

  const elId = id + 'inpEl_';

  function onInputChange(el) {
    if (el == 4) {
      return false;
    }
    let n = el + 1;
    const c = document.getElementById(elId + el);
    if (c.value) {
      const e = document.getElementById(elId + n);
      e.focus();
    }
  }

  return (
    <div className="SplitInput">
      <div className="columns is-centered">
        {
          [1, 2, 3, 4].map(e => (
            <div className="column is-narrow" key={e}>
              <input type="input" id={elId + e} onInput={() => { onInputChange(e) }} />
            </div>
          ))
        }
      </div>
    </div>
  );
};

export default SplitInput;