import React from 'react';
import { SwiperSlide } from 'swiper/react';

import bannerImg from '../../../assets/imgs/banner.png';

import AppSlider from '../appSlider/AppSlider';


const Banners = () => {
  return (
    <div>
      <AppSlider
        slidesPerView={4.2}
        pagination={true}
      >
        {
          [1, 2, 3, 4, 5, 6, 7, 8, 9].map((e) => (
            <SwiperSlide key={e}>
              <div>
                <img src={bannerImg} />
              </div>
            </SwiperSlide>
          ))
        }
      </AppSlider>
    </div>
  );
};

export default Banners;