import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import './Signup.css';

const Signup = () => {

  const history = useHistory();

  const [form, setForm] = useState({
    name: '',
    phone: '',
    email: '',
    password: '',
    confirmPassword: '',
    touchedInp: false
  });

  const [formValid, setFormValid] = useState({
    name: { status: true, msg: '', inpErrCss: '' },
    phone: { status: true, msg: '', inpErrCss: '' },
    email: { status: true, msg: '', inpErrCss: '' },
    password: { status: true, msg: '', inpErrCss: '' },
    confirmPassword: { status: true, msg: '', inpErrCss: '' }
  });

  useEffect(() => {
    if (form.touchedInp) {
      setFormValid(formValidation());
    }
  }, [form]);

  function setFormValue(ev, key) {
    setForm({ ...form, [key]: ev.target.value, touchedInp: true });
  }

  function formValidation() {
    const f = form;
    const s = {};

    // RESET FORM VALIDATION
    Object.keys(formValid).forEach(e => {
      s[e] = { status: true, msg: '', inpErrCss: '' };
    });

    if (!f.name) {
      s.name.status = false;
      s.name.msg = 'Please provide Name';
      s.name.inpErrCss = 'is-danger';
    }
    if (!f.phone) {
      s.phone.status = false;
      s.phone.msg = 'Please provide Phone Number';
      s.phone.inpErrCss = 'is-danger';
    }
    if (!f.email) {
      s.email.status = false;
      s.email.msg = 'Please provide Email';
      s.email.inpErrCss = 'is-danger';
    }
    if (!f.password) {
      s.password.status = false;
      s.password.msg = 'Please provide Password';
      s.password.inpErrCss = 'is-danger';
    }
    if (!f.confirmPassword) {
      s.confirmPassword.status = false;
      s.confirmPassword.msg = 'Please Confirm Password';
      s.confirmPassword.inpErrCss = 'is-danger';
    }
    if (f.password && f.password != f.confirmPassword) {
      s.confirmPassword.status = false;
      s.confirmPassword.msg = 'Password is not matching';
      s.confirmPassword.inpErrCss = 'is-danger';
    }
    return s;
  }

  function submit(ev) {
    ev.preventDefault();
    const s = formValidation();
    const c = Object.keys(s).reduce((sum, i) => {
      if (!s[i].status) {
        sum += 1;
      }
      return sum;
    }, 0);
    if (c === 0) {
      history.push('/auth/login');
    } else {
      setFormValid(s);
    }
  }

  return (
    <div className="Signup">

      <div className="left-bg" style={{ flexGrow: 1 }}>
        <div>Grocery App</div>
      </div>

      <div style={{ flexGrow: 1, padding: '30px' }}>
        <div className="card">
          <div className="card-content">
            <h1 className="title mb-0">Let's get started</h1>
            <div>Create account to see top picks for you!</div>

            <form className="mt-5" autoComplete="off">

              <div className="field">
                <label className="label">Name</label>
                <div className="control">
                  <input className={'input ' + formValid.name.inpErrCss} type="text" value={form.name} onChange={(e) => { setFormValue(e, 'name') }} placeholder="Enter your Name" />
                </div>
                {formValid.name.msg && <p className="help is-danger">{formValid.name.msg}</p>}
              </div>

              <div className="field">
                <label className="label">Phone Number</label>
                <div className="control">
                  <input className={'input ' + formValid.phone.inpErrCss} type="number" value={form.phone} onChange={(e) => { setFormValue(e, 'phone') }} placeholder="Enter your Phone Number" />
                </div>
                {formValid.phone.msg && <p className="help is-danger">{formValid.phone.msg}</p>}
              </div>

              <div className="field">
                <label className="label">Email</label>
                <div className="control">
                  <input className={'input ' + formValid.email.inpErrCss} type="email" value={form.email} onChange={(e) => { setFormValue(e, 'email') }} placeholder="Enter your email" />
                </div>
                {formValid.email.msg && <p className="help is-danger">{formValid.email.msg}</p>}
              </div>

              <div className="field">
                <label className="label">Password</label>
                <div className="control">
                  <input className={'input ' + formValid.password.inpErrCss} type="password" value={form.password} onChange={(e) => { setFormValue(e, 'password') }} placeholder="Enter your password" />
                </div>
                {formValid.password.msg && <p className="help is-danger">{formValid.password.msg}</p>}
              </div>

              <div className="field">
                <label className="label">Confirm Password</label>
                <div className="control">
                  <input className={'input ' + formValid.confirmPassword.inpErrCss} type="password" value={form.confirmPassword} onChange={(e) => { setFormValue(e, 'confirmPassword') }} placeholder="Confirm your password" />
                </div>
                {formValid.confirmPassword.msg && <p className="help is-danger">{formValid.confirmPassword.msg}</p>}
              </div>

              <button className="button is-fullwidth app-primary-btn" onClick={submit}>Create Account</button>

              <div className="m-4 has-text-centered">or</div>

              <button className="button is-fullwidth">Sign up with Google</button>

              <div className="has-text-centered mt-5">
                <small>Already have an account? <Link to="/auth/login" replace={true}>Sign in</Link> </small>
              </div>

            </form>
          </div>
        </div>
      </div>


    </div>
  );
};

export default Signup;